.home-recent-articles-ctn {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  /* padding: 7%;*/
}

.home-recent-articles-ctn-content {
  background: whitesmoke;
  padding: 7%;
}

.home-recent-articles-title {
  margin: 0;
  font-size: var(--txt-title-xl);
  color: var(--purple);
  font-family: 'Caveat', cursive;
  text-shadow: 5px 5px 3px var(--grey);
}

.home-recent-articles-ctn-subtitle {
  display: flex;
  justify-content: space-between;
}

.home-recent-articles-subtitle {
  font-size: var(--txt-p-xl);
  color: var(--gray);
  text-align: justify;
}

.home-recent-articles-subtitle-button {
  text-decoration: none;
  color: #fff;
  padding: 12px;
  background: var(--green);
  width: 220px;
  height: 32px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: var(--txt-p-xl);
  margin-left: 150px;
}

.home-recent-articles-ctn-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 22px;
  margin-top: 72px;
}

.home-recent-articles-ctn-card {
  flex: 1 0 30%;
  background: #fff;
}

.home-recent-articles-ctn-card-image {
  position: relative;
}

.home-recent-articles-card-image {
  width: 100%;
  height: 100%;
  aspect-ratio: 4/3;
  object-fit: cover;
}

.home-recent-articles-card-date {
  background: var(--green);
  color: #fff;
  font-size: var(--txt-p-sm);
  position: absolute;
  bottom: 12px;
  left: 12px;
  padding: 7px;
  border-radius: 5px;
}

.home-recent-articles-ctn-card-info {
  padding: 12px 32px;
}

.home-recent-articles-card-info-date {
  font-size: var(--txt-p-sm);
  color: var(--gray);
}

.home-recent-articles-card-info-title {
  font-size: var(--txt-p-xl);
  font-weight: 700;
  margin-bottom: 0;
}

.home-recent-articles-card-info-category {
  font-size: var(--txt-p-sm);
  color: var(--gray);
  margin-top: 5px;
}